@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video, button {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	font-family: 'Roboto';
	color: #94a1b2;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

html, body, #root, #app {
	height: 100%;
	width: 100%;
	background-color: #242629;
}

main {
    height: 100%;
    margin: 0 auto;
}

.loader {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 151px;
    height: 87px;
    font-size: 0;
    margin: 0 auto 0 auto;
}
.loader > .elem {
    display: inline-block;
    vertical-align: middle;
    width: 12px;
    height: 12px;
    background: transparent;
    border-radius: 50px;
    -webkit-animation: anim-loader 2.5s ease-in-out infinite;
            animation: anim-loader 2.5s ease-in-out infinite;
}
.loader > .elem:nth-child(2) {
    margin-left: 12px;
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}
.loader > .elem:nth-child(3) {
    margin-left: 12px;
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}
.loader > .elem:nth-child(4) {
    margin-left: 12px;
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}
.loader > .elem:nth-child(5) {
    margin-left: 12px;
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
}
.loader > .elem:nth-child(6) {
    margin-left: 12px;
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}
.loader > .elem:nth-child(7) {
    margin-left: 12px;
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
}
@-webkit-keyframes anim-loader {
    0% {
        height: 12px;
        box-shadow: none;
   }
    30% {
        height: 70px;
        box-shadow:  -5px 5px 10px #1f2023, 5px -5px 10px #292c2f;
   }
    60% {
        height: 12px;
        box-shadow: none;
   }
}
@keyframes anim-loader {
    0% {
        height: 12px;
        box-shadow: none;
   }
    30% {
        height: 70px;
        box-shadow:  -5px 5px 10px #1f2023, 5px -5px 10px #292c2f;
   }
    60% {
        height: 12px;
        box-shadow: none;
   }
}

::-webkit-scrollbar {
    width: 2px;
    height: 2px;
}
::-webkit-scrollbar-button {
    width: 0px;
    height: 0px;
}
::-webkit-scrollbar-thumb {
    background: #e1e1e1;
    border: 0px none #ffffff;
    border-radius: 50px;
}
::-webkit-scrollbar-thumb:hover {
    background: #ffffff;
}
::-webkit-scrollbar-thumb:active {
    background: #000000;
}
::-webkit-scrollbar-track {
    background: rgba(0,0,0,0);
    border: 0px none #ffffff;
    border-radius: 50px;
}
::-webkit-scrollbar-track:hover {
    background: rgba(0,0,0,0);
}
::-webkit-scrollbar-track:active {
    background: #333333;
}
::-webkit-scrollbar-corner {
    background: transparent;
}


#launches {
    height: 100%;
}

.fake-header {
    height: 100px;
    background-color: #242629;
}

.month-name {
    text-align: center;
    margin: 20px 0 15px 0;
    color: #FFFFFE;
    font-weight: 700;
    text-transform: capitalize;
    font-size: 1.3em;
}

#launches div.launches-list {
    padding: 0 10px;
    padding-top: 25px;
    min-height: calc(100% - 300px);
    overflow-y: auto;
    overflow-x: hidden;
    display: flex;
    flex-direction: column;
    padding-bottom: 95px;
}
#launches div.launch-item {
    border-radius: 65px;
    margin-left: auto;
    margin-right: auto;
    height: 100px;
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    width: -webkit-fill-available;
    width: -moz-available;
    width: stretch;
    max-width: 900px;
    padding: 15px;
    display: flex;
    flex-direction: row;
    overflow: hidden;
    margin-bottom: 30px;
    background: linear-gradient(225deg, #202225, #27292c);
    box-shadow:  -5px 5px 6px #1e2022, 5px -5px 6px #2a2c30;
    cursor: pointer;
    transition: 0.3s;
}
#launches div.launch-item:hover {
    transition: 0.3s;
    transform: scale(1.1);
}
.image-container {
    height: 100%;
    width: 100px;
    border-radius: 50px;
    overflow: hidden;
    background-position: top;
    background-size: cover;
    background-repeat: no-repeat;
}
.launch-info {
    padding-left: 10px;
    width: calc(100% - 110px);
}
.launch-info h2 {
    font-weight: 700;
    color: #FFFFFE;
}
.launch-info p {
    font-size: 0.8em;
    margin-top: 15px;
}
.launch-info .date {
    text-transform: capitalize;
    color: #FFFFFE;
}
.launch-info .date span {
    font-size: 0.8em;
}
.launch-info a {
    color: #7f5af0;
    font-size: 0.8em;
    margin-top: 7px;
    display: block;
    text-decoration: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}


@media only screen and (min-width: 800px) {
    .launch-info a {
        display: inline-block;
        margin-right: 20px;
        margin-top: 15px;
    }
}
header {
    height: 80px;
    display: flex;
    flex-direction: row;
    width: 100%;
    background: linear-gradient(225deg, #202225, #27292c);
    box-shadow:  -5px 5px 6px #1e2022, 5px -5px 6px #2a2c30;
    width: calc(100% - 20px);
    max-width: 930px;
    margin: 10px auto 0 auto;
    border-radius: 50px;
}
header div {
    width: calc(100%/2);
    display: flex;
    padding: 0 40px;
    justify-content: flex-start;
}

header div.author-credits {
	font-size: 0.7em;
	opacity: 0.5;
	text-align: center;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
}
.author-credits span {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.author-credits span:first-of-type {
    margin-bottom: 5px;
}

h1 {
    font-size: 1.2em;
    display: flex;
    align-items: center;
    justify-content: center;
	font-weight: 700;
    color: #FFFFFE;
    text-align: center;
}


@media only screen and (max-width: 730px) {
    header {
        flex-direction: column;
        justify-content: center;
    }
    header div {
        width: 100%;
        padding: 0;
        justify-content: center;
    }
    header div.author-credits {
        align-items: center;
    }
    header h1 {
        margin-bottom: 7px;
    }
}
.search-container {
    margin: 25px auto 0 auto;
    width: calc(100% - 20px);
    max-width: 800px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
}

.search-container button {
    width: 110px;
    text-align: center;
    cursor: pointer;
    background: #242629;
    background: linear-gradient(225deg, #202225, #27292c);
    box-shadow:  -5px 5px 10px #1b1d1f, 5px -5px 10px #2d2f33;
    border: none;
    border-radius: 20px;
    outline: none;
    font-size: 0.9em;
    transition: 0.3s;
    color: #FFFFFE;
    margin: 0 5px;
}
.search-container button svg {
    height: 10px;
}
.search-container button img {
    height: 10px;
}
.search-container button img.soyuz {
    height: 15px;
}
.search-container button:hover {
    box-shadow: none;
    background: #242629;
    transition: 0.3s;
}

.search-input-container {
    background: #242629;
    box-shadow: inset -5px 5px 6px #1e2022, inset 5px -5px 6px #2a2c30;
    border-radius: 20px;
    height: 40px;
    color: #FFFFFE;
    display: flex;
    width: calc(100% - 40px);
}

.search-input {
    outline: none;
    padding: 0 20px;
    border: none;
    height: 100%;
    width: calc(100% - 60px);
    border-radius: 20px 0 0 20px;
    background-color: rgba(0,0,0,0);
    color: #FFFFFE;
    font-size: 16px;
}

.search-input-container label,
.search-input-container span {
    height: 100%;
    width: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search-input-container label svg,
.search-input-container span svg {
    cursor: pointer;
    height: 15px;
    color: #FFFFFE;
}

.filters-container {
    margin-top: 20px;
    height: 40px;
    display: flex;
    flex-direction: row;
}


#launch-item-page-container {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #242629;
    overflow: hidden;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    overflow-y: auto;
    background-color: #16161a;
}
#launch-item-page-container.no-bg .card-content {
    margin-bottom: 0;
}

#launch-item-page-container .card-header {
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    padding-right: 5px;
}
.card-header button {
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
    height: 40px;
    position: fixed;
}
.card-header svg {
    height: 25px;
    color: #FFFFFE;
}

#launch-item-page-container .card-content {
    background-color: #242629;
    padding: 0 0 50px 0;
    margin-bottom: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.card-content > * {
    max-width: 900px;
}
.card-content section {
    margin-top: 50px;
}

.card-content h1 {
    font-size: 2.5em;
    font-weight: 700;
    margin: 50px 0;
}
.card-content h2 {
    font-size: 2.2em;
    font-weight: 700;
    color: #FFFFFE;
    text-align: center;
    margin: 0 0 20px 0;
}
.card-content h3 {
    color: #FFFFFE;
    font-size: 1.1em;
    margin-bottom: 10px;
    text-align: center;
    padding: 0 10px;
}
.card-content .countdown {
    display: flex;
    background: linear-gradient(225deg, #202225, #27292c);
    box-shadow:  -5px 5px 6px #1e2022, 5px -5px 6px #2a2c30;
    padding: 20px 30px;
    border-radius: 50px;
}
.card-content .countdown div {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.card-content .countdown div span:first-of-type,
.card-content .countdown div.separator{
    font-size: 35px;
    color: #FFFFFE;
}
div.separator {
    margin: 0 13px;
}
.countdown div span:last-of-type{
    font-size: 12px;
}
.countdown div span {
    color: #7f5af0;
}
#launch-item-page-container section > div{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
#launch-item-page-container section p {
    font-size: 1.2em;
}
section div.date {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    padding: 20px 0;
    background: linear-gradient(225deg, #202225, #27292c);
    box-shadow:  -5px 5px 6px #1e2022, 5px -5px 6px #2a2c30;
    width: 210px;
    height: 170px;
    border-radius: 50px;
}
section div.date span.day {
    text-transform: capitalize;
}
section div.date span.hours {
    font-weight: 700;
}
section div.date span.utc {
    font-size: 12px;
    padding-top: 7px;
}
#launch-item-page-container section > div.row {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    margin-top: 20px;
    flex-wrap: wrap;
    width: 700px;
}
#launch-item-page-container div.row div.date {
    margin: 0 10px;
}

#launch-item-page-container .custom-section-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    background: linear-gradient(225deg, #202225, #27292c);
    box-shadow:  -5px 5px 6px #1e2022, 5px -5px 6px #2a2c30;
    width: 210px;
    height: 210px;
    border-radius: 50px;
    overflow: hidden;
}

#launch-item-page-container .section-container {
    background: linear-gradient(225deg, #202225, #27292c);
    box-shadow:  -5px 5px 6px #1e2022, 5px -5px 6px #2a2c30;
    padding: 30px;
    border-radius: 50px;
    margin: 0 10px;
}
#launch-item-page-container section .section-container .mission-name {
    color: #FFFFFE;
    font-size: 20px;
    text-align: center;
    margin-bottom: 10px;
    font-weight: 700;
}
#launch-item-page-container section .section-container .mission-type {
    font-size: 0.9em;
    text-align: center;
    margin-bottom: 25px;
}
#launch-item-page-container section .section-container .mission-desc {
    text-align: center;
    line-height: 25px;
}
.section-container .mission-wiki {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0 0 0;
}
.custom-section-container .country-flag,
.custom-section-container .rocket-img {
    width: 100%;
    height: 85px;
    overflow: hidden;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.custom-section-container .country-name,
.custom-section-container .rocket-name {
    color: #FFFFFE;
    font-size: 20px;
    text-align: center;
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: 700;
}
#launch-item-page-container section .custom-section-container .country-location-name,
#launch-item-page-container section .custom-section-container .rocket-agency {
    font-size: 0.9em;
    text-align: center;
    margin-bottom: 15px;
    padding: 0 20px;
}
.custom-section-container .links,
.section-container .links {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
}
.custom-section-container .links a,
.section-container .links a {
    margin: 5px;
}

@media only screen and (max-width: 730px) {
    #launch-item-page-container section > div.row {
        width: 100%;
    }
    #launch-item-page-container section > div.row > div.date,
    #launch-item-page-container section > div.row > div.section-container {
        margin-top: 15px;
        margin-bottom: 15px;
    }
}
